  box-shadow: 0 0 0 2px #c7c7c7;
}

#cfw-shipping-info-action {
  justify-content: flex-end;
}

#cfw-shipping-details-fields {
  margin-bottom: 15px;
}

#cfw-shipping-details-fields label {
  width: 100px;
  display: table-cell;
  font-weight: 700;
}

#cfw-shipping-details-fields span {
  display: table-cell;
}

.cfw-prev-tab {
  transition: all .2s ease;
  color: #9e9e9e;
  text-decoration: none;
}

.cfw-bottom-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5em;
}

.cfw-bottom-controls .cfw-primary-btn {
  display: inline-block;
  text-decoration: none;
  background: #232323;
  color: #fff;
  border-radius: 4px;
  border: none;
  font-size: 1em;
  box-sizing: border-box;
  font-weight: 400;
  -webkit-transition: all .3s ease-in-out;
  -moz-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
  padding: 19px 15px;
}

.cfw-bottom-controls .cfw-primary-btn:hover {
  background: #ddd;
  color: #666;
}

.cfw-def-action-btn {
  border: none;
  box-shadow: none;
  border-radius: 4px;
  cursor: pointer;
  padding: 12px 16px;
  font-size: 1em;
  background: #999;
  color: #f4f4f4;
  text-align: center;
  width: 100%;
  transition: all .1s ease-in-out;
}

.cfw-def-action-btn:hover {
  background: #000;
}

.cfw-cart-row {
  display: flex;
  align-items: flex-start;
  margin-bottom: 0.7em;
}

.cfw-cart-row .cfw-cart-item-image img {
  width: auto;
  height: auto;
  max-width: 100%;
  border-radius: 3px;
  border: 1px solid #e1e1e1;
}

.cfw-input-container .cfw-input-wrap {
  margin-bottom: 1em;
}

.cfw-input-container .cfw-input-wrap:last-child {
  margin-bottom: 0;
}

.cfw-module {
  margin-bottom: 1.8em;
}

.cfw-input-label {
  user-select: none;
  pointer-events: none;
  position: absolute;
  color: #9e9e9e;
  transition: all .2s ease-out;
  font-size: 12px;
  margin-top: .5em;
  padding: 0 1.02em;
  line-height: 1.625;
  opacity: 0;
}

.cfw-input-label abbr[title] {
  text-decoration: none;
}

.cfw-module-title {
  font-size: 1.5em;
  color: #333;
  font-weight: 300;
  margin-bottom: 0.6em;
}

.cfw-have-acc-text {
  margin-bottom: 0.6em;
}

.cfw-left-column {
  order: 0;
}

.cfw-right-column {
  order: 1;
  border-left: 1px solid #eee;
  padding: 20px;
  padding-top: 0;
  box-sizing: border-box;
}

#cfw-main-container .apple-pay-button-checkout-separator {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 20px;
  display: none;
}

#cfw-main-container .apple-pay-button-wrapper {
  text-align: center;
  margin-bottom: 10px;
}

@media (max-width: 767px) {
  .cfw-prev-tab {
    margin-bottom: 1em;
    display: inline-block;
  }

  .previous-button {
    text-align: center;
  }

  #cfw-cart-details {
    order: 0;
    border-left: none;
    width: 100% !important;
    padding: 30px !important;
    background: #f4f4f4;
    margin-bottom: 30px !important;
  }

  #cfw-cart-details.active #cfw-cart-details-review-bar {
    margin-bottom: 30px;
  }

  #cfw-cart-details.active #cfw-cart-details-arrow {
    transform: rotate(-90deg);
  }

  #cfw-cart-details-review-bar {
    display: flex;
    margin-bottom: 0;
  }

  #cfw-cart-details-review-bar > div:last-child {
    text-align: right;
  }

  #cfw-cart-details-review-bar > div:first-child {
    display: flex;
  }

  #cfw-cart-details-review-bar > div:first-child:hover #cfw-cart-details-arrow,
  #cfw-cart-details-review-bar > div:first-child:hover a {
    fill: #d79915;
    color: #d79915;
  }

  #cfw-cart-details-review-bar a {
    text-decoration: none;
    color: #ebb134;
  }

  #cfw-cart-details-collapse-wrap {
    display: none;
  }

  #cfw-show-cart-details {
    display: flex;
  }

  #cfw-cart-details-arrow {
    transition: all .2s linear;
    transform: rotate(90deg);
    width: 20px;
    height: 20px;
    cursor: pointer;
    margin-left: 6px;
    fill: #ebb134;
  }

  #cfw-totals-list {
    margin-bottom: 0;
  }

  #cfw-tab-container {
    order: 1;
  }

  #cfw-cart-details,
  #cfw-tab-container {
    padding-left: 0;
    padding-right: 0;
    width: 90%;
    margin: auto;
  }

  #cfw-breadcrumb {
    display: none;
  }

  .cfw .cfw-input-wrap-row {
    margin: 0;
    width: 100%;
  }

  .cfw .cfw-input-wrap-row > div {
    width: 100%;
    float: none;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 1em;
  }

  .cfw .cfw-input-wrap-row:last-child > div {
    margin-bottom: 0;
  }

  .cfw .wc-credit-card-form .cfw-input-wrap-row > div {
    margin-bottom: 1em;
  }

  .cfw .wc-credit-card-form .cfw-input-wrap-row > div:last-child {
    margin-bottom: 0;
  }

  .cfw-bottom-controls {
    flex-direction: column;
  }

  .cfw-bottom-controls .cfw-primary-btn {
    width: 100%;
    text-align: center;
  }
}

@media (max-width: 445px) {
  #cfw-cart-details-review-bar {
    font-size: 0.875em;
  }

  #cfw-cart-details-arrow {
    width: 18px;
    height: 18px;
  }
}

#cfw-footer {
  text-align: center;
  background: #fff;
  color: #999999;
  font-size: 0.825em;
}

#cfw-footer .cfw-footer-inner {
  padding: 40px 0;
}




